import "./portfolio.scss"
// bibliotek
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import {useRef, useEffect} from "react";
import { Link } from "react-router-dom";

// images
import portfolio_bg from "../../assets/bg_main_logo_img/bg.jpg"
// картинки
import portfolio_item_1 from "../../assets/portfolio/4.jpg";
import portfolio_item_2 from "../../assets/portfolio/2.jpg";
import portfolio_item_3 from "../../assets/portfolio/3.jpg";
import portfolio_item_4 from "../../assets/portfolio/1.jpg";
import portfolio_item_5 from "../../assets/portfolio/5.jpg";
import portfolio_item_6 from "../../assets/portfolio/6.jpg";
import portfolio_item_7 from "../../assets/portfolio/7.jpg";
import portfolio_item_8 from "../../assets/portfolio/8.jpg";

import buttonImg from "../../assets/portfolio/button/arrow1.svg"
// components

gsap.registerPlugin(ScrollTrigger);
function Portfolio() {
// анимация синего фона (шарики)
    const imgRef =useRef(null);
    useEffect(()=>{
        const el = imgRef.current;
        gsap.fromTo(el, {y:"10%"}, {y: "-100%", duration:3, scrollTrigger: {
        trigger: el,
        scrub: 1,
        start: "top bottom",
        end: "+=1200",
        }})
    }, [])
// аниамция большого текста
    const portfolioText =useRef(null);
    useEffect(()=>{
        const el = portfolioText.current;
        gsap.fromTo(el, {opacity: 0, scale: 0.5}, { scale:1, opacity: 1, duration:3, scrollTrigger: {
        trigger: el,
        scrub:1,
        start: "top 80%",
        end: "+=400",
        }})
    }, []);
     // анимация картинок
    const sectionRef_1 =useRef(null);
    useEffect(()=>{
        const el = sectionRef_1.current;
        gsap.fromTo(el, {x:"100",}, {x:0, duration:3, scrollTrigger: {
            trigger: el,
            start: "top 80%",
            end: "+=800",
            scrub: 1,
            delay:1,
        }});
    }, []);
    const sectionRef_2 =useRef(null);
    useEffect(()=>{
        const el = sectionRef_2.current;
        gsap.fromTo(el, {x:"-100"}, { x:0, duration:3, scrollTrigger: {
            trigger: el,
            start: "top 80%",
            end: "+=800",
            scrub: 1,
            delay:1,
        }});
    }, []);

    const sectionRef_3 =useRef(null);
    useEffect(()=>{
        const el = sectionRef_3.current;
        gsap.fromTo(el, {x:"100",}, {x:0, duration:3, scrollTrigger: {
            trigger: el,
            start: "top 80%",
            end: "+=800",
            scrub: 1,
            delay:1,
        }});
    }, []);

    const sectionRef_4 =useRef(null);
    useEffect(()=>{
        const el = sectionRef_4.current;
        gsap.fromTo(el, {x:"-100"}, { x:0, duration:3, scrollTrigger: {
            trigger: el,
            start: "top 80%",
            end: "+=800",
            scrub: 1,
            delay:1,
        }});
    }, []);
    const sectionRef_5 =useRef(null);
    useEffect(()=>{
        const el = sectionRef_5.current;
        gsap.fromTo(el, {x:"100"}, { x:0, duration:3, scrollTrigger: {
            trigger: el,
            start: "top 80%",
            end: "+=800",
            scrub: 1,
            delay:1,
        }});
    }, []);
    const sectionRef_6 =useRef(null);
    useEffect(()=>{
        const el = sectionRef_6.current;
        gsap.fromTo(el, {x:"-100"}, { x:0, duration:3, scrollTrigger: {
            trigger: el,
            start: "top 80%",
            end: "+=800",
            scrub: 1,
            delay:1,
        }});
    }, []);
    const sectionRef_7 =useRef(null);
    useEffect(()=>{
        const el = sectionRef_7.current;
        gsap.fromTo(el, {x:"100"}, { x:0, duration:3, scrollTrigger: {
            trigger: el,
            start: "top 80%",
            end: "+=800",
            scrub: 1,
            delay:1,
        }});
    }, []);
    const sectionRef_8 =useRef(null);
    useEffect(()=>{
        const el = sectionRef_8.current;
        gsap.fromTo(el, {x:"-100"}, { x:0, duration:3, scrollTrigger: {
            trigger: el,
            start: "top 80%",
            end: "+=800",
            scrub: 1,
            delay:1,
        }});
    });

    //функция отправляет страницу вверх
    const scrollToTop = () => {
        window.scrollTo(0, 0)
    }
    return(
        <>
        <div className="portfolio_bg" > 
            <img ref={imgRef} className="portfolio_bg_img" src={portfolio_bg} alt="" />
            <div className="portfolio_bg_wrapper" >
                <h2 ref={portfolioText} className="font-BeatriceBold " >Gallery</h2>
                <div className="portfolio_bg_grid">
                  <div className="portfolio_bg_grid_1" ref={sectionRef_1}><img src={portfolio_item_1} alt=""/></div>
                  <div className="portfolio_bg_grid_2" ref={sectionRef_2}><img src={portfolio_item_2} alt="" /></div>
                  <div className="portfolio_bg_grid_3" ref={sectionRef_3}><img src={portfolio_item_3} alt="" /></div>
                  <div className="portfolio_bg_grid_4" ref={sectionRef_4}><img src={portfolio_item_4} alt="" /></div>
                  <div className="portfolio_bg_grid_5" ref={sectionRef_5}><img src={portfolio_item_5} alt="" /></div>
                  <div className="portfolio_bg_grid_6" ref={sectionRef_6}><img src={portfolio_item_6} alt="" /></div>
                  <div className="portfolio_bg_grid_7" ref={sectionRef_7}><img src={portfolio_item_7} alt="" /></div>
                  <div className="portfolio_bg_grid_8" ref={sectionRef_8}><img src={portfolio_item_8} alt="" /></div>
                </div>
                <button className="portfolio_bg_button" onClick={scrollToTop}><Link to="/picture"><h6>All projects</h6><img src={buttonImg} alt="рщн" /></Link></button>
            </div>
        </div>
        </>
    )
}

export default Portfolio;